.image {
  display: inline-block;
  box-sizing: border-box;
  background: #232323;
}

.image_cover,
.image_contain,
.image_center {
  width: 100%;
  height: 100%;
}

.image_cover {
  object-fit: cover;
}

.image_contain {
  object-fit: contain;
}

.image_center {
  object-fit: none;
  object-position: center center;
}
